body{
  font-size: 24px;
  padding-left: 10px;
  padding-right: 10px;
  min-width: 1000px;
}

img{
  max-height:200px;
  border-radius: 5px;
}

h2,h3{
  text-align: center;
}

.top-bar{
  position:fixed;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.site-title{
  width:20%;
  text-align: center;
  color: #6E90B7;
}

.search-field{
  width:78%;
  margin-right:15px;
  background-color: #DFE9EE;
  border-radius: 0 0 15px 15px;
}

.sort{
  cursor: pointer;
}

.button-page-nav:focus{
  background-color: white;
  outline: none;
  border-color: #ccc;
}

.button-page-nav:hover{
  background-color: #E6E6E6;
}

th:nth-child(1){
  width:50px;
}

th:nth-child(2){
  width:150px;
}

.trends{
  width:100%;
  height: 50px;
  background-color: grey;
  
}

.content-page{
  display: inline-block;
  width: 80%;
}

.search{
  float:right;
  margin:15px 10px 0 0;
  width: 200px;
  position:relative;
}

.fas{
  position:absolute;
  right:10px;
  top: 8px;
  cursor:pointer;
}

.form-control{
  display: inline-block;
  padding-right: 40px;
}

.panel-group{
  margin-bottom: 0;
}

/*
loader
*/


.wrap-loader:before {
  display: inline-block;
  height: 100vh;
  vertical-align: middle;
  content: "";
}

.lds-facebook {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  width: 64px;
  height: 64px;
  left: 50%;
  top:45%;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 6px;
  width: 13px;
  background: grey;
  -webkit-animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
          animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 6px;
  -webkit-animation-delay: -0.24s;
          animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 26px;
  -webkit-animation-delay: -0.12s;
          animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 45px;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
@-webkit-keyframes lds-facebook {
  0% {
    top: 6px;
    height: 51px;
  }
  50%, 100% {
    top: 19px;
    height: 26px;
  }
}
@keyframes lds-facebook {
  0% {
    top: 6px;
    height: 51px;
  }
  50%, 100% {
    top: 19px;
    height: 26px;
  }
}
/*
end loader
*/

.wrapper-button-page-nav{
  text-align: center;
  margin-left: 50%;
}

.btn-toolbar{
  display: inline-block;
  width: 400px;
}

/* Table */

table{
  width: 95%;
  margin: 70px 0 0 28.5%;
  border-spacing: 5px 15px;
  border-collapse: separate;
}

th:first-child{
  border-radius: 10px 0 0 0;
}

th:last-child{
  border-radius: 0 10px 0 0;
}

th:nth-child(n+4){
  width:10%;
}

th{
  font-family: 'Sunflower', sans-serif;
  padding: 3px;
  text-align: center;
  background-color: #6C91BB;
}

td:not(:nth-child(3)){
 text-align: center;
}

td:nth-child(3){
  padding: 0 10px 0 10px;
  max-width:60%;
  vertical-align: top;
  text-align: justify;
}

td img{
 max-width: 100%;
}

td h3{
  margin-top:0;
}

tr{
  margin-top:10px;
  border-bottom: 1px solid black;
}

.overview{
  font-size: 14px;
}

/* categories */

.categories{
  position:fixed;
  display: inline-block;
  width: 20%;
  vertical-align: top;
  top: 70px;

}

.category{
  font-size: 16px;
  display: block;
  cursor: pointer;
  color: black;
  text-decoration: none;
  text-align: center;
  border-radius: 10px;
  margin: 5px 5px 0 5px;
}

.category:hover{
  background-color: grey;
  text-decoration: none;
  color: white;
}

.categories-title{
  height: 34px;
  background-color: #DFE9EE;
  border-radius: 10px 10px 0 0;
}

.categories-title h3{
  margin-top: 15px;
  padding-top: 4px;
}

.categories-main{
  margin-top: 15px;
  padding:5px 2px 5px 2px;
  background-color: #DFE9EE;
  border-radius: 0 0 10px 10px;
}

.first-column{
  display: inline-block;
  width:50%;
}

.second-column{
  display: inline-block;
  width:50%;  
}




